//
// Layout Config
//
@import "../../../../const.scss";

// Root Font Sizes
$root-font-size: 13px; // Root font size for desktop mode
$root-font-size-lg: 13px; // Root font size for tablet mode
$root-font-size-md: 12px; // Root font size for mobile mode

// Layout Theme Colors
$layout-themes: (
        dark: #ffffff, // Dark theme color
        light: #ffffff // Light theme color
) !default;

// Page Background Color
$page-bg: #EEF0F8 !default;

// Page Paddings
$page-padding: (
        desktop: 10px, // Padding for desktop mode
        tablet-and-mobile: 10px // Padding for tablet and mobile modes
) !default;

// Header
$header-config: (
  // Desktop Mode
        desktop: (
          // Default Mode
                default: (
                        height: 65px
                ),
          // Fixed Mode
                fixed: (
                        zindex: 97,
                        height: 65px
                )
        ),
  // Tablet & Mobile Modes
        tablet-and-mobile: (
          // Default Mode
                default: (
                        height: 55px,
                ),
          // Fixed Mode
                fixed: (
                        zindex: 97,
                        height: 55px
                )
        )
) !default;

