// Subheader
$subheader-config: (
  // Default Mode
        default: (
                bg-color: $white, // Subheader's solid background color
                border-color: $gray-200, // Subheader's separator border color
        ),
  // Fixed Subheader Mode
        fixed: (
                height: 54px, // Fixed subheader's height
                height-mobile: 65px, // Fixed mobile subheader's height
                shadow: 0px 10px 30px 0px rgba(82, 63, 105, 0.08), // Fixed subheader's box shadow
                zindex: 95 // Fixed subheader's z-index
        )
) !default;

// Footer
$footer-config: (
  // Fixed Footer Mode
        fixed: (
                zindex: 97, // Fixed footer's z-index
                height: 60px, // Fixed footer's height
                shadow: 0px 0px 28px 0px rgba(82, 63, 105, 0.13) // Fixed fooer box shadow
        )
) !default;

// Offcanvas
$offcanvas-config: (
        width: 375px, // Offcanvas panel's width
        zindex: 1001, // Offcanvas panel's z-index
        shadow: 0px 1px 9px -3px rgba(0, 0, 0, 0.25), // Offcanvas panel's box shadow
        bg-color: #ffffff, // Offcanvas panel's Background color
        overlay-bg-color: rgba(#000000, 0.1) // Offcanvas panel's overlay background color
) !default;
